}

.LogReg h1 {
	font-weight: bold;
	margin: 0;
}

.LogReg p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.LogReg span {
	font-size: 12px;
}

.LogReg a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.LogReg button {
	border-radius: 20px;
	border: 1px solid #FF4B2B;
	background-color: #FF4B2B;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

.LogReg button:active {
	transform: scale(0.95);
}

.LogReg button:focus {
    outline: none;
    cursor: pointer;
}

.LogReg button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

.LogReg .container {
