}

.portfolio-tab {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 150px;
    transition: all 2050ms cubic-bezier(0.175, 0.885, 0.320, 1.275);
}

.portfolio-tab>.column {
    float: left;
    margin-bottom: 30px;
    margin-left: -13px;
    position: relative;
    z-index: 2;
    -ms-perspective: 900px;
    perspective: 900px;
    transform-style: preserve-3d;
    -webkit-filter: grayscale(1);
    filter: grayscale(1);
}

.portfolio-tab>.column:hover {
    -webkit-filter: grayscale(0);
    filter: grayscale(0);
}

.portfolio-tab>.column>.prow {
    -ms-perspective: 900px;
    perspective: 900px;
    transform-style: flat;
    padding: 10px;
}

/* FIX BUG CHROME */

@media screen and (-webkit-min-device-pixel-ratio: 0) {
    .portfolio-tab>.column>.prow {
        -ms-perspective: none;
        perspective: none;
        padding: 0 10px;
    }
}

.portfolio-tab>.column>.prow>div {
    width: 200px;
    height: 260px;
    margin-bottom: 5px;
    background-color: #ccc;
    overflow: hidden;
    position: relative;
    transform-style: flat;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center, center center;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
    transition: all 450ms cubic-bezier(0.175, 0.885, 0.320, 1.275);
}

.portfolio-tab>.column>.prow .thumb-nb,
.portfolio-tab>.column>.prow .thumb-color {
    width: 315px;
    height: 270px;
    background-color: #000;
    background-size: cover;
    background-position: center center, center center;
    transition: all 850ms cubic-bezier(0.250, 0.460, 0.450, 0.940);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 8;
}

.portfolio-tab>.column>.prow .thumb-nb {
    z-index: 9
}

.portfolio-tab>.column>.prow:hover>div>.thumb-nb {
    opacity: 0;
    transform: scale(1.15);
}

.portfolio-tab>.column>.prow .shadow {
    width: 315px;
    height: 270px;
    position: absolute;
    z-index: 10;
    left: 0;
    top: 0;
}

.portfolio-tab:nth-child(even)>.column>.prow1>div {
    transform: rotateY(-20deg);
    transform-origin: left center;
}

.portfolio-tab:nth-child(even)>.column>.prow2>div {
    transform: rotateY(20deg);
    transform-origin: right center;
}

.portfolio-tab:nth-child(odd)>.column>.prow1>div {
    transform: rotateY(20deg);
    transform-origin: right center;
}

.portfolio-tab:nth-child(odd)>.column>.prow2>div {
    transform: rotateY(-20deg);
    transform-origin: left center;
}

.portfolio-tab:nth-child(even)>.column>.prow2>div>.shadow,
.portfolio-tab:nth-child(odd)>.column>.prow1>div>.shadow {
    background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#80000000', GradientType=1);
}

.portfolio-tab>.column>.prow>div>.shadow {
    transition: all 650ms cubic-bezier(0.165, 0.840, 0.440, 1.000);
}

.portfolio-tab>.column>.prow>div>.shadow {
    opacity: 1;
}

#portfolio>.portfolio-tab>.column>.prow:hover>div>.shadow {
    opacity: 0;
}

.portfolio-tab>.column>.prow:hover>div {
    transform: rotateY(0deg);
}

.portfolio-tab>.column>.prow>div>.description {
    position: absolute;
    z-index: 20;
    bottom: -80px;
    left: 0;
    width: 100%;
    height: auto;
    text-align: center;
    padding: 20px 5px;
    background-color: rgba(0, 0, 0, 0.75);
    color: #fff;
    transition: all 450ms cubic-bezier(0.175, 0.885, 0.320, 1.275) 240ms;
}

.portfolio-tab>.column>.prow:hover>div>.description {
    bottom: 0;
}

.portfolio-tab>.column>.prow>div>.description>p {
    margin: 0;
    padding: 0;
}

.portfolio-tab>.column>.prow>div>.linkIcon {
    position: absolute;
    z-index: 20;
    top: 20%;
    left: 50%;
    margin-left: -44px;
    width: 88px;
    height: 88px;
    background-image: url(/img/background.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    transition: all 450ms cubic-bezier(0.175, 0.885, 0.320, 1.275) 300ms;
    transform: scale(0) rotate(90deg);
    opacity: 0;
}

